.wp-block-create-block-suggested-blog-posts h2,
.wp-block-create-block-suggested-blog-posts p {
	margin-bottom: 20px;
}

.wp-block-create-block-suggested-blog-posts li {
	list-style-type: none;
	margin-top: 10px;
}

.wp-block-create-block-suggested-blog-posts ul {
	padding-inline-start: 0;
}

@media only screen and (max-width: 399px) {
	.wp-block-create-block-suggested-blog-posts h2 {
		font-size: 1.6rem;
	}

	.wp-block-create-block-suggested-blog-posts p {
		font-size: 1.1rem;
	}

	.wp-block-create-block-suggested-blog-posts a {
		font-size: 0.9rem;
	}
}

@media only screen and (min-width: 400px) {
	.wp-block-create-block-suggested-blog-posts h2 {
		font-size: 2.1rem;
	}

	.wp-block-create-block-suggested-blog-posts p {
		font-size: 1.3rem;
	}

	.wp-block-create-block-suggested-blog-posts a {
		font-size: 1rem;
	}
}
